<template>
  <div class="main-page">
    <div class="main" :class="{'online':online}">
      <div class="inner">
        <div class="logo">
          <img v-if="online" src="../../images/img-connection.png" alt="">
          <img v-else src="../../images/img-no_connection.png" alt="">
        </div>
        <dl>
          <dt>{{$t('home.h1')}}</dt>
          <dd>
            <div v-if="online" class="button" @click="disconnect">{{$t('home.online')}}</div>
            <div v-else class="button" @click="connection">{{$t('home.nolink')}}</div>
          </dd>
        </dl>
      </div>
    </div>
    <div class="container">
      <div class="userinfo">
        <div class="title">{{$t('home.title')}}</div>
        <div class="people">
          <div class="avatar" @click="goAccountInfo"></div>
          <dl>
            <dt>{{$t('home.username')}}</dt>
            <dd>{{userInfo.username}}</dd>
          </dl>
        </div>
        <ul>
          <li>
            <dl>
              <dt>{{$t('home.accounttate')}}：</dt>
              <dd>{{userInfo.type}}</dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt>{{$t('home.endTime')}}：</dt>
              <dd>{{userInfo.endTime}}</dd>
            </dl>
          </li>
        </ul>
      </div>
      <ul class="banner">
        <li>
          <div class="button" @click="recharge"><span>{{$t('home.recharge')}}</span></div>
        </li>
        <li>
          <div class="button" @click="showDialog"><span>{{$t('home.card')}}</span></div>
        </li>
        <li>
          <div class="button" @click="signOut"><span>{{$t('home.signOut')}}</span></div>
        </li>
      </ul>
    </div>

    <el-dialog
      :visible.sync="centerDialogVisible"
      class="dialog-card"
      :modal-append-to-body="false"
    >
      <span class="title">{{$t('home.cardNum')}}</span>
      <el-form class="card-form" autoComplete="on" :model="cardForm" :rules="cardRules" ref="cardForm">
        <el-form-item :label="$t('home.placeholder')" prop="cardInfo"
                      :class="{'noLabel': !cardForm.cardInfo, 'onFocus':cardType.cardInfo === 1}">
          <el-input name="cardInfo" type="text" v-model="cardForm.cardInfo" :placeholder="$t('home.placeholder')"
                    @focus="cardType.cardInfo = 1" @blur="cardType.cardInfo = 0"/>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button type="primary" @click.native.prevent="submitCard"
                   :disabled="!isCardNum">{{$t('home.exchange')}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { isvalidCardNum } from 'common/js/validate'
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'index',
  data () {
    const validateCard = (rule, value, callback) => {
      if (!isvalidCardNum(value)) {
        this._data.isCardNum = false
        callback(new Error(' '))
      } else {
        this._data.isCardNum = true
        callback()
      }
    }
    return {
      userInfo: {
        username: '13666666666',
        type: 'VIP会员',
        endTime: '2018-05-22 10:15:42'
      },
      centerDialogVisible: false,

      cardForm: {
        cardInfo: ''
      },
      cardRules: {
        cardInfo: [
          {required: true, trigger: 'blur', validator: validateCard},
          {required: true, trigger: 'change', validator: validateCard}
        ]
      },
      // 0：无 1：输入中 2：出错：3：完成
      cardType: {
        cardInfo: ''
      },
      isCardNum: ''
    }
  },
  computed: {
    ...mapGetters([
      'online'  // 未连接：false 已连接：true
    ])
  },
  created () {
    console.log('index')
  },
  methods: {
    // 立即连接
    connection () {
      this.setOnline(true)
    },
    // 断开连接
    disconnect () {
      this.setOnline(false)
    },
    // 会员续费
    recharge() {
      window.location.href='http://www.baidu.com'
    },
    // 显示使用点卡框
    showDialog () {
      this.centerDialogVisible = true
    },
    // 兑换
    submitCard () {
      this.centerDialogVisible = false
    },
    // 退出
    signOut () {

    },
    //
    goAccountInfo () {
      this.$router.push({path: '/account'})
    },
    ...mapMutations({
      setOnline: 'SET_ONLINE'
    })
  },
  components: {}
}


</script>

<style rel="stylesheet/scss" lang="scss">
@import "common/styles/variable.scss";
/* reset element-ui css */
.dialog-card {
  .card-form {
    width: 300px;
    .el-form-item {
      border: none;
      margin-bottom: 0;
      &.noLabel label {
        visibility: hidden;
      }
      & label::before {
        display: none;
      }
      .el-input {
        display: inline-block;
        width: 300px;
        height: 36px;
        input {
          background: transparent;
          border: 0px;
          -webkit-appearance: none;
          border-radius: 0px;
          padding: 9px 5px 9px 0;
          color: $gray_dark;
          height: 36px;
          font-size: 18px;
          border-bottom: 1px solid $gray_line;
          &::-webkit-input-placeholder {
            color: $gray_placeholder;
          }
        }
      }
      .el-input__inner:focus {
        border-bottom: 1px solid $gray_line_on;
      }
      label {
        color: $gray_placeholder;
        font-size: 12px;
        line-height: 12px;
        padding: 8px 0 0 0;
      }
      &.is-error {
        label {
          visibility: visible;
          color: $red;
        }
        .el-input__inner {
          border-bottom: 1px solid $red;
        }
        .el-input__inner:focus {
          border-bottom: 1px solid $gray_line_on;
        }
      }
      &.onFocus {
        label {
          visibility: visible;
          color: $gray_line_on;
        }
      }
    }
  }
  .el-dialog {
    width: 480px;
    height: 240px;
    margin: 0 auto;
    position: absolute;
    top: 50%;
    left: 50%;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.12);
    transform: translate(-50%, -50%);
    margin-top: 0 !important;
    .el-dialog__headerbtn {
      top: 8px;
      right: 8px;
      i {
        width: 24px;
        height: 24px;
        background: url("../../images/ico-close.png") no-repeat left top;
        &:before {
          display: none;
        }
      }
    }
    .el-dialog__body {
      padding-top: 35px;
      padding-bottom: 22px;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      .title {
        font-size: 18px;
        line-height: 36px;
        color: $gray_dark;
      }
    }
    .el-dialog__footer {
      display: flex;
      justify-content: center;
      .el-button {
        width: 160px;
        height: 42px;
        font-size: 18px;
        color: $white;
        background: $gray_line_on;
        border-radius: 4px;
        box-shadow: none;
        border: none;
        &.is-disabled {
          background: $gray_line;
          color: $gray_light;
        }
      }
    }
  }
}


</style>

<style rel="stylesheet/scss" lang="scss" scoped>
@import "common/styles/variable.scss";
.baima-container {
  .main-page {
    height: calc(100% - 48px);
    display: flex;
    flex-direction: column;
    .main {
      flex: 0 0 45vh;
      height: 45vh;
      min-height: 272px;
      width: 100%;
      background: $orange;
      position: relative;
      .inner {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        .logo {
          flex: 0 0 140px;
          width: 140px;
          margin-right: 32px;
        }
        dl {
          flex: 1;
          display: flex;
          flex-direction: column;
          justify-content: center;
          dt {
            font-size: 36px;
            font-weight: bold;
            color: $white;
            margin-bottom: 24px;
          }
          dd {
            .button {
              width: 180px;
              height: 48px;
              line-height: 48px;
              background: $white;
              text-align: center;
              font-size: 20px;
              color: $orange;
              border-radius: 24px;
              cursor: pointer;
            }
          }
        }
      }
      &.online {
        background: $blue;
        .inner {
          .button {
            color: $blue;
          }
        }
      }
    }
    .container {
      flex: 1 0 calc(100% - 45vh - 48px);
      height: calc(100% - 45vh - 48px);
      min-height: 280px;
      padding: 16px;
      display: flex;
      box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12) inset;
      .userinfo {
        flex: 0 0 59.5vw;
        width: 59.5vw;
        min-width: 512px;
        border-radius: 4px;
        background: $white;
        box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
        margin-right: 16px;
        padding: 32px 32px 30px 32px;
        display: flex;
        flex-direction: column;
        align-items: center;
        .title {
          flex: 1 0 24px;
          width: 100%;
          height: 24px;
          font-size: 24px;
          color: $gray_dark;
          margin-bottom: 24px;
        }
        .people {
          flex: 1 0 48px;
          width: 100%;
          height: 48px;
          display: flex;
          align-items: center;
          margin-bottom: 10px;
          .avatar {
            flex: 0 0 48px;
            width: 48px;
            height: 48px;
            background: url("../../images/img-avatar.png") no-repeat left top;
            margin-right: 12px;
            cursor: pointer;
          }
          dl {
            display: flex;
            flex-direction: column;
            justify-content: center;
            dt {
              font-size: 16px;
              color: $gray;
              margin-bottom: 6px;
            }
            dd {
              font-size: 20px;
              font-weight: bold;
              color: $gray_dark;
            }
          }
        }
        ul {
          flex: 1 0 86px;
          width: 100%;
          height: 86px;
          display: flex;
          flex-direction: column;
          li {
            flex: 0 0 42px;
            height: 42px;
            border-bottom: 1px solid $gray_line;
            dl {
              display: flex;
              flex-direction: row;
              height: 16px;
              line-height: 42px;
              dt {
                color: $gray_light;
              }
              dd {
                color: $gray_dark;
              }
            }
          }
        }
      }
      .banner {
        flex: 1;
        display: flex;
        flex-direction: column;
        li {
          flex: 1;
          width: 100%;
          min-height: 72px;
          margin-top: 16px;
          &:first-child {
            margin-top: 0;
          }
          .button {
            width: 100%;
            height: 100%;
            border-radius: 4px;
            box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
            position: relative;
            vertical-align: center;
            cursor: pointer;
            &:after {
              position: absolute;
              top: 50%;
              right: 20px;
              margin-top: -10px;
              display: block;
              content: "";
              width: 14px;
              height: 20px;
              background: url("../../images/ico-arrow-righ.png") no-repeat left top;
            }
            span {
              position: absolute;
              top: 50%;
              left: 60px;
              transform: translate(0, -50%);
              font-size: 18px;
              color: $gray_dark;
            }
          }
          &:nth-of-type(1) .button {
            background: url("../../images/ico-diamond.png") no-repeat $white 20px center;
          }
          &:nth-of-type(2) .button {
            background: url("../../images/ico-money.png") no-repeat $white 20px center;
          }
          &:nth-of-type(3) .button {
            background: url("../../images/ico-sign_out.png") no-repeat $white 20px center;
          }
        }
      }
    }
  }
}
</style>



